// @flow
import React from 'react';
import type { Element } from 'react';
import Form from 'components/Forms';
import { useHistory } from 'react-router-dom';
import { APP_PATHS } from 'shared/Consts';

type Props = {
  offerId: string,
  step: string,
  formId: string,
};

const Step1 = (props: Props): Element<typeof Content> => {
  const { offerId, formId } = props;
  const history = useHistory();

  const saveAndExitAction = () => {
    history.push({ path: APP_PATHS.dashboard });
  };

  return (
    <Form itemId={offerId} formId={formId} saveExitAction={saveAndExitAction} />
  );
};

export default Step1;
